		tagCode: function tagCode(text) {
			var out = '';
			var open = false;
			for (var c = 0; c < text.length; c++) {
				if (text[c] === '`') {
					if (!open) {
						out += '<span class="tag is-code">';
					} else {
						out += '</span>';
					}
					open = !open;
				} else {
					out += text[c];
				}
			}
			if (open) {
				out += '</span>';
			}
			return out;
		},

